import React from "react";

const BuildingDetail = (props) => {
  console.log(props);
  if (props.IdAdr !== undefined) {
    return (
      "<h2>"+props.IdAdr+"</h2>"+
      "<p>"+props.EgenTypkod_typ+"</p>"+
      "<h4>Primärenergital: "+props.EgiPrimarenergital+" kWh/m²</h4>"+
      "<p>Byggår: "+props.EgenNybyggAr+"</p>"
    )}
    else { return "<h4>Ingen energideklaration</h4>"
  }
};

export default BuildingDetail;
